import _ from 'lodash';
import React from 'react';
import { Button } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { Attack } from 'src/lib/attacks';
import Unit, { UnitID } from 'src/lib/Unit';
import { RootReduxState } from 'src/reducers';
import {
    endCurrentUnitTurn,
    placeUnit,
    selectAttack,
    startGame,
    undoMovement,
    unPlaceCurrentUnit,
} from 'src/reducers/boardReducer';
import { defaultUnits } from 'src/reducers/inventoryReducer';
import { useBoard } from '../../common/redux';

const AttackButton: React.FC<{
    attack: Attack;
    selectedAttack: Attack | null;
}> = ({ attack, selectedAttack }) => {
    const dispatch = useDispatch();
    let selectedClass = '';
    if (selectedAttack && selectedAttack.name === attack.name) {
        selectedClass = 'selected-attack';
    }
    return (
        <div className={'attack ' + selectedClass} onClick={() => dispatch(selectAttack(attack))}>
            {attack.name}
        </div>
    );
};

const placedCounts = (units: Unit[]) => {
    return units.reduce(
        (acc: Record<UnitID, number>, unit) => ({ ...acc, [unit.unitId]: (acc[unit.unitId] || 0) + 1 }),
        defaultUnits
    );
};

const PlaceUnitRow: React.FC<{ unitId: UnitID; placed: Record<UnitID, number> }> = ({ unitId, placed }) => {
    const inventory = useSelector((state: RootReduxState) => state.inventory);
    const dispatch = useDispatch();
    const amountLeft = inventory.ownedUnits[unitId] - placed[unitId];
    return (
        <div>
            {unitId} x{amountLeft}{' '}
            {amountLeft > 0 && <Button onClick={() => dispatch(placeUnit(unitId))}>Place</Button>}
        </div>
    );
};

const PlaceUnitButtons: React.FC = () => {
    const board = useBoard();
    const dispatch = useDispatch();
    const inventory = useSelector((state: RootReduxState) => state.inventory);
    if (!board.setupPhase || !board.selectedUploadLocation) {
        return null;
    }
    const placed = placedCounts(board.playerUnits);
    const unitIds = (Object.keys(inventory.ownedUnits) as UnitID[]).filter(
        (key: UnitID) => inventory.ownedUnits[key] > 0
    );
    const placedUnitExistsHere = _.some(
        board.playerUnits,
        unit => board.selectedUploadLocation && unit.isOn(board.selectedUploadLocation)
    );
    return (
        <>
            <h4>setupPhase</h4>
            {unitIds.map((unitId, index) => (
                <PlaceUnitRow key={index} unitId={unitId} placed={placed} />
            ))}
            {placedUnitExistsHere && (
                <div className='attack' onClick={() => dispatch(unPlaceCurrentUnit())}>
                    Unplace
                </div>
            )}
        </>
    );
};

const UnitInfo: React.FC = () => {
    const board = useBoard();
    const dispatch = useDispatch();
    const unit = board.selectedUnit;
    if (!unit) {
        return null;
    }

    return (
        <>
            <h3>{unit.name}</h3>
            {unit.finished && <div>Turn Finished</div>}
            <div>Health: {unit.tiles.length}</div>
            <div>Max Health: {unit.maxLength}</div>
            <div>
                Move: {unit.movesTaken}/{unit.moves}
            </div>
            <div>attack: {JSON.stringify(board.selectedAttack)}</div>
            {unit.getAttacks().map((attack, index) => (
                <AttackButton key={index} selectedAttack={board.selectedAttack} attack={attack} />
            ))}
            {!board.setupPhase && (
                <>
                    <div className='attack' onClick={() => dispatch(endCurrentUnitTurn())}>
                        End Turn
                    </div>
                    <div className='attack' onClick={() => dispatch(undoMovement())}>
                        Undo
                    </div>
                </>
            )}
        </>
    );
};

const Infobar: React.FC = () => {
    const board = useBoard();
    const dispatch = useDispatch();
    return (
        <div className='infobar semi-cover-bg'>
            <h4>{board.currentTurn}</h4>
            <PlaceUnitButtons />
            <UnitInfo />
            {board.setupPhase && board.playerUnits.length > 0 && (
                <div className='attack' onClick={() => dispatch(startGame())}>
                    Start Game
                </div>
            )}
        </div>
    );
};

export default Infobar;
