import _ from 'lodash';
import { tileDistance } from 'src/common/helpers';
import allAttacks, { Attack, AttackName } from './attacks';
import { BoardLocation } from './types';

const baseInfo = {
    player_unit1: {
        image: 'r',
        imageFile: null,
        tiles: [],
        color: 'orange',
        name: 'the_unit',
        maxLength: 5,
        moves: 4,
        attacks: ['attack1', 'attack2'] as AttackName[],
    },
    cpu_unit1: {
        image: 'Z',
        imageFile: null,
        tiles: [],
        color: 'lightblue',
        name: 'the_bad_guy',
        maxLength: 6,
        moves: 2,
        attacks: ['attack2'] as AttackName[],
    },
    hard_drive: {
        image: 'hard_drive',
        tiles: [],
        imageFile: 'hard_drive.png',
        color: 'rgb(174, 173, 146)',
        name: 'hard_drive',
        maxLength: 3,
        moves: 4,
        attacks: ['attack1'] as AttackName[],
    },
    rainbow_table: {
        image: 'rainbow_table',
        imageFile: 'rainbow_table.png',
        tiles: [],
        color: 'rgb(123, 180, 86)',
        name: 'rainbow_table',
        maxLength: 5,
        moves: 2,
        attacks: ['attack1'] as AttackName[],
    },
    // private key
    //   key with army hat
    // scope
    // cookies
    // cluster
    // MEAN ELK LAMP stack
    // /dev/null
};

export type UnitID = keyof typeof baseInfo;

type UnitConnection = [BoardLocation, BoardLocation];
class Unit {
    public attacks: AttackName[];
    public color: string;
    public finished = false;
    public image: string | null;
    public imageFile: string | null;
    public maxLength: number;
    public moves: number;
    public movesTaken = 0;
    public name: string;
    public startingTiles: BoardLocation[] = [];
    public tiles: BoardLocation[];
    public unitId: UnitID;

    constructor(type: UnitID, tiles: BoardLocation[]) {
        const attrs = baseInfo[type];
        if (!attrs) {
            console.warn('cannot find unit type ' + type);
        }
        this.attacks = attrs.attacks;
        this.color = attrs.color;
        this.image = attrs.image;
        this.imageFile = attrs.imageFile;
        this.maxLength = attrs.maxLength;
        this.moves = attrs.moves;
        this.name = attrs.name;
        this.tiles = attrs.tiles;
        this.tiles = tiles;
        this.unitId = type;

        this.startTurn();
    }

    getAttacks() {
        return this.attacks.map(atk => allAttacks[atk]);
    }

    head() {
        return this.tiles[0];
    }

    isOn(location: BoardLocation) {
        return _.some(this.tiles, unitTile => _.isEqual(unitTile, location));
    }

    removeTiles(amount: number) {
        if (amount <= 0) return;
        this.tiles = _.take(this.tiles, this.tiles.length - amount);
    }

    canMoveTo(location: BoardLocation) {
        return tileDistance(this.head(), location) === 1;
    }

    moveTo(location: BoardLocation) {
        const newTiles = _.reject(this.tiles, unitTile => _.isEqual(unitTile, location));
        newTiles.unshift(location);
        this.tiles = newTiles;
        this.removeTiles(this.tiles.length - this.maxLength);
        this.movesTaken += 1;
    }

    canAttack(loc: BoardLocation, _attack: Attack | null) {
        // todo
        return this.canMoveTo(loc);
    }

    performAttack(attack: Attack, otherUnit: Unit) {
        if (attack.type === 'standard') {
            otherUnit.removeTiles(attack.damage);
        }
        this.endTurn();
    }

    hasMovementLeft() {
        return this.movesTaken < this.moves && !this.finished;
    }

    startTurn() {
        this.movesTaken = 0;
        this.finished = false;
        this.startingTiles = _.cloneDeep(this.tiles);
    }

    endTurn() {
        this.finished = true;
    }

    resetMovement() {
        this.movesTaken = 0;
        this.tiles = _.cloneDeep(this.startingTiles);
    }

    movesRemaining() {
        return this.moves - this.movesTaken;
    }

    // TODO: make this not return dupes
    getConnectors() {
        const connectors: UnitConnection[] = [];
        this.tiles.forEach(tile1 => {
            this.tiles.forEach(tile2 => {
                if (tileDistance(tile1, tile2) === 1) {
                    connectors.push([tile1, tile2]);
                }
            });
        });
        return connectors;
    }
}

export default Unit;
