import React from 'react';
import { useBoard } from '../../common/redux';
import _ from 'lodash';

import MouseLayer from './MouseLayer';
import Layer from './Layer';
import { Board, BoardLocation, Tile } from 'src/lib/types';
import Unit from 'src/lib/Unit';
import { Button } from 'react-bootstrap';
import { useDispatch } from 'react-redux';
import { changeRoute } from 'src/reducers/routerReducer';
import { locationsNextTo } from 'src/common/helpers';

const TileBackground: React.FC<{ tile: Tile }> = ({ tile }) => {
    if (!tile.exists) {
        return null;
    }
    return <rect x={tile.x * 46} y={tile.y * 46} width='40' height='40' style={{ fill: 'white', opacity: 0.25 }} />;
};

const TileLayer: React.FC<{ tiles: Tile[] }> = ({ tiles }) => {
    return (
        <Layer index={1}>
            {tiles.map((tile, i) => (
                <TileBackground key={i} tile={tile} />
            ))}
        </Layer>
    );
};

const UnitComp: React.FC<{ unit: Unit }> = ({ unit }) => {
    if (unit.tiles.length === 0) {
        return null;
    }

    const headImage = unit.imageFile && (
        <ImageComp location={unit.tiles[0]} href={`/game123/assets/units/${unit.imageFile}`} />
    );
    const headText = (
        <text x={unit.tiles[0][0] * 46} y={unit.tiles[0][1] * 46 + 40} textLength='40'>
            {unit.image}
        </text>
    );

    return (
        <>
            {unit.tiles.map((tile, i) => (
                <rect key={i} x={tile[0] * 46} y={tile[1] * 46} width='40' height='40' fill={unit.color} />
            ))}
            {headImage || headText}
            {unit.finished && (
                <>
                    <circle cx={unit.tiles[0][0] * 46 + 40} cy={unit.tiles[0][1] * 46} r='7' fill='#ffffff' />
                    <text x={unit.tiles[0][0] * 46 + 34} y={unit.tiles[0][1] * 46 + 6} r='7' textLength='14'>
                        
                    </text>
                </>
            )}
            {unit.getConnectors().map((connection, i) => {
                const [loc1, loc2] = connection;
                const xSpot = Math.min(loc1[0], loc2[0]) * 46;
                const ySpot = Math.min(loc1[1], loc2[1]) * 46;
                const horiz = loc1[1] === loc2[1];
                return (
                    <rect
                        key={i}
                        x={xSpot + (horiz ? 40 : 13)}
                        y={ySpot + (horiz ? 13 : 40)}
                        width={horiz ? 6 : 14}
                        height={horiz ? 14 : 6}
                        fill={unit.color}
                    />
                );
            })}
        </>
    );
};

const ImageComp: React.FC<{ location: BoardLocation; href: string; opacity?: string | undefined }> = ({
    location,
    href,
    opacity,
}) => {
    return <image href={href} width='40' height='40' x={location[0] * 46} y={location[1] * 46} opacity={opacity} />;
};

const UnitLayer: React.FC<{ units: Unit[] }> = ({ units }) => {
    return (
        <Layer index={2}>
            {units.map((unit, i) => (
                <UnitComp key={i} unit={unit} />
            ))}
        </Layer>
    );
};

const renderMovementIndicators = (board: Board) => {
    const units = _.concat(board.playerUnits, board.cpuUnits);
    if (
        !board.selectedUnit ||
        !board.selectedUnit.hasMovementLeft() ||
        board.selectedAttack ||
        !board.selectedUnit.head()
    ) {
        return null;
    }
    const otherUnitsTiles = _.flatMap(
        units.filter(unit => unit !== board.selectedUnit),
        'tiles'
    );

    const indicators = locationsNextTo(board.selectedUnit.head(), board.selectedUnit.movesRemaining()).filter(
        ([x, y]) => {
            const found = _.find(board.tiles, loc => loc.x === x && loc.y === y);
            return (
                found && found.exists && _.every(otherUnitsTiles, otherUnitsTile => !_.isEqual(otherUnitsTile, [x, y]))
            );
        }
    );
    return indicators.map((loc, index) => (
        <ImageComp key={index} href='/game123/assets/movement.png' opacity='0.3' location={loc} />
    ));
};

const renderAttackIndicators = (board: Board) => {
    if (!board.selectedAttack) {
        return null;
    }
    const indicators = locationsNextTo(board.selectedUnit!.head()).filter(([x, y]) => {
        const found = _.find(board.tiles, loc => loc.x === x && loc.y === y);
        return (
            found &&
            found.exists &&
            _.every(board.selectedUnit!.tiles, selectedUnitTile => !_.isEqual(selectedUnitTile, [x, y]))
        );
    });
    return indicators.map((loc, index) => <ImageComp key={index} href='/game123/assets/target.png' location={loc} />);
};

const renderUploadSpots = (board: Board) => {
    if (!board.setupPhase) {
        return null;
    }
    return (
        <>
            {board.uploadLocations.map(
                (loc, index) =>
                    !board.playerUnits.some(unit => unit.isOn(loc)) && (
                        <ImageComp key={index} href='/game123/assets/upload.png' location={loc} />
                    )
            )}
        </>
    );
};

const EffectsLayer: React.FC = () => {
    const board = useBoard();
    return (
        <Layer index={3}>
            {renderMovementIndicators(board)}
            {renderAttackIndicators(board)}
            {renderUploadSpots(board)}
        </Layer>
    );
};

const EndPopup: React.FC<{ isWon: boolean; isLost: boolean }> = ({ isWon, isLost }) => {
    const dispatch = useDispatch();
    if (!isWon && !isLost) {
        return null;
    }
    return (
        <div style={{ width: 690, height: 460, zIndex: 5, position: 'relative', paddingTop: 160 }}>
            <div
                style={{
                    height: 100,
                    margin: 'auto',
                    textAlign: 'center',
                    padding: 10,
                    border: '3px solid #111',
                    width: 200,
                    backgroundColor: '#323',
                }}
            >
                You {isWon ? 'win' : 'lose'}
                <br />
                <Button onClick={() => dispatch(changeRoute('/level-select'))}>Go back</Button>
            </div>
        </div>
    );
};

const GameBoard: React.FC = () => {
    const board = useBoard();
    return (
        <div className='board-wrapper semi-cover-bg'>
            <TileLayer tiles={board.tiles} />
            <UnitLayer units={_.concat(board.playerUnits, board.cpuUnits)} />
            <EffectsLayer />
            <MouseLayer />
            <EndPopup isWon={board.isWon} isLost={board.isLost} />
        </div>
    );
};

export default GameBoard;
