import React from 'react';
import { useSelector } from 'react-redux';
import { RootReduxState } from 'src/reducers';
import routes from './routes';

const CustomRouter: React.FC = () => {
    const routerState = useSelector((state: RootReduxState) => state.route);
    const CurrentRoute = routes[routerState.currentRoute];

    if (CurrentRoute) {
        return <CurrentRoute />;
    } else {
        console.warn(`Route did not match - '${routerState.currentRoute}'`);
        return <span />;
    }
};

export default CustomRouter;
