import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import _ from 'lodash';
import { Attack } from 'src/lib/attacks';
import { Level } from 'src/lib/levels';
import { Board, BoardLocation } from 'src/lib/types';
import Unit, { UnitID } from 'src/lib/Unit';

const initialState: Board = {
    selectedUnit: null,
    selectedAttack: null,
    tiles: [],
    playerUnits: [],
    cpuUnits: [],
    uploadLocations: [],
    selectedUploadLocation: null,
    currentTurn: 'playerUnits',
    setupPhase: true,
    isWon: false,
    isLost: false,
};

const handleSelectUploadLocation = (state: Board, clickedTile: BoardLocation) => {
    if (!state.setupPhase) {
        return;
    }
    const isUploadLocation = _.some(state.uploadLocations, loc => _.isEqual(loc, clickedTile));
    state.selectedUploadLocation = isUploadLocation ? clickedTile : null;
};

const handleMovement = (state: Board, clickedTile: BoardLocation, clickedUnit: Unit | null) => {
    if (state.setupPhase || !state.selectedUnit || state.selectedAttack) {
        return false;
    } // if not movement mode

    if (
        state.selectedUnit &&
        state.selectedUnit.hasMovementLeft() &&
        (!clickedUnit || clickedUnit === state.selectedUnit)
    ) {
        const boardTile = _.find(state.tiles, tile => tile.x === clickedTile[0] && tile.y === clickedTile[1]);
        if (!boardTile) {
            throw new Error();
        }
        if (boardTile.exists && !isAnyOtherUnitOn(state, clickedTile) && state.selectedUnit.canMoveTo(clickedTile)) {
            state.selectedUnit.moveTo(clickedTile);
            if (state.selectedUnit.movesRemaining() === 0) {
                state.selectedAttack = state.selectedUnit.getAttacks()[0];
            }
            return true;
        }
    }
    return false;
};

const handleAttack = (state: Board, clickedTile: BoardLocation, clickedUnit: Unit | null) => {
    if (state.setupPhase || !state.selectedAttack || !state.selectedUnit || state.selectedUnit.finished) {
        return false;
    } // if not attack mode
    if (!state.selectedUnit.canAttack(clickedTile, state.selectedAttack)) {
        return false;
    }

    if (clickedUnit) {
        state.selectedUnit.performAttack(state.selectedAttack, clickedUnit);
    } else {
        state.selectedUnit.endTurn();
    }
    state.selectedAttack = null;
    return true;
};

const removeUnitAtLoc = (units: Unit[], loc: BoardLocation) => {
    return units.filter(unit => !_.isEqual(unit.tiles, [loc]));
};

_.forEach(initialState.cpuUnits, unit => unit.endTurn());
const boardSlice = createSlice({
    name: 'board',
    initialState,
    reducers: {
        boardMouseClick: (state, action: PayloadAction<{ x: number; y: number }>) => {
            const clickedTile = [action.payload.x, action.payload.y] as BoardLocation;
            const clickedUnit =
                _.find(_.concat(state.playerUnits, state.cpuUnits), unit => unit.isOn(clickedTile)) ?? null;

            handleSelectUploadLocation(state, clickedTile);
            const attacked = handleAttack(state, clickedTile, clickedUnit);
            const moved = handleMovement(state, clickedTile, clickedUnit);
            if (!attacked && !moved) {
                state.selectedAttack = null;
                state.selectedUnit = clickedUnit;
            }
            checkOtherStatuses(state);
        },

        endCurrentUnitTurn: state => {
            state.selectedUnit!.endTurn();
            checkOtherStatuses(state);
        },

        selectAttack: (state, action: PayloadAction<Attack>) => {
            state.selectedAttack = action.payload;
            checkOtherStatuses(state);
        },

        undoMovement: state => {
            if (state.selectedUnit) {
                state.selectedUnit.resetMovement();
            }
            state.selectedAttack = null;
            checkOtherStatuses(state);
        },

        selectLevel: (_state, action: PayloadAction<Level>) => {
            const newBoard = action.payload.initializeBoard();
            newBoard.cpuUnits.forEach(unit => unit.endTurn());
            return newBoard;
        },

        startGame: state => {
            if (state.playerUnits.length === 0) {
                return;
            }
            state.setupPhase = false;
        },

        unPlaceCurrentUnit: state => {
            if (!state.selectedUploadLocation) {
                return;
            }
            state.playerUnits = removeUnitAtLoc(state.playerUnits, state.selectedUploadLocation);
            state.selectedUnit = null;
        },

        placeUnit: (state, action: PayloadAction<UnitID>) => {
            if (!state.selectedUploadLocation) {
                return;
            }
            state.playerUnits = removeUnitAtLoc(state.playerUnits, state.selectedUploadLocation);
            const newUnit = new Unit(action.payload, [[...state.selectedUploadLocation]]);
            state.playerUnits = [...state.playerUnits, newUnit];
            state.selectedUnit = newUnit;
        },
    },
});

function isAnyOtherUnitOn({ playerUnits, cpuUnits, selectedUnit }: Board, tile: BoardLocation) {
    const tiles = _(playerUnits)
        .concat(cpuUnits)
        .reject(unit => unit === selectedUnit)
        .flatMap('tiles')
        .value();
    return _.some(tiles, t => _.isEqual(t, tile));
}

function checkOtherStatuses(state: Board) {
    if (state.setupPhase) {
        return;
    }
    const currentTeam = state[state.currentTurn];
    if (_.every(currentTeam, 'finished')) {
        console.log('finished', state.currentTurn);
        const nextTeam = state.currentTurn === 'playerUnits' ? 'cpuUnits' : 'playerUnits';
        _.forEach(state[nextTeam], unit => unit.startTurn());
        state.currentTurn = nextTeam;
        state.selectedUnit = state[nextTeam][0];
        state.selectedAttack = null;
    } else if (state.selectedUnit?.finished) {
        state.selectedUnit = currentTeam.find(unit => !unit.finished) || null;
    }
    state.playerUnits = state.playerUnits.filter(unit => unit.tiles.length > 0);
    state.cpuUnits = state.cpuUnits.filter(unit => unit.tiles.length > 0);
    state.isWon = state.cpuUnits.length === 0;
    state.isLost = state.playerUnits.length === 0;
}

export const {
    boardMouseClick,
    selectAttack,
    undoMovement,
    selectLevel,
    endCurrentUnitTurn,
    placeUnit,
    startGame,
    unPlaceCurrentUnit,
} = boardSlice.actions;
export default boardSlice.reducer;
