import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { UnitID } from 'src/lib/Unit';

export type Inventory = {
    ownedUnits: Record<UnitID, number>;
};

export const defaultUnits: Record<UnitID, number> = {
    player_unit1: 0,
    cpu_unit1: 0,
    hard_drive: 0,
    rainbow_table: 0,
};

const initialState: Inventory = {
    ownedUnits: {
        ...defaultUnits,
        player_unit1: 2,
        hard_drive: 2,
        rainbow_table: 2,
    },
};

const inventorySlice = createSlice({
    name: 'inventory',
    initialState,
    reducers: {
        buyUnit: (state, action: PayloadAction<UnitID>) => {
            state.ownedUnits[action.payload]++;
        },
    },
});

export const { buyUnit } = inventorySlice.actions;
export default inventorySlice.reducer;
