import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useBoard } from 'src/common/redux';
import { playAsComputer } from 'src/lib/ai';
import { Tile } from 'src/lib/types';
import { boardMouseClick } from 'src/reducers/boardReducer';

import Layer from './Layer';

const MouseTile: React.FC<{ tile: Tile }> = ({ tile }) => {
    const dispatch = useDispatch();
    if (!tile.exists) {
        return null;
    }
    return (
        <rect
            x={tile.x * 46}
            y={tile.y * 46}
            width='40'
            height='40'
            className='mouse-tile'
            onClick={() => dispatch(boardMouseClick({ x: tile.x, y: tile.y }))}
        />
    );
};

const MouseLayer: React.FC = () => {
    const board = useBoard();
    const dispatch = useDispatch();

    useEffect(() => {
        if (board.currentTurn === 'cpuUnits' && !board.isWon && !board.isLost) {
            setTimeout(() => playAsComputer(board, dispatch), 500);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [board, dispatch]);

    if (board.currentTurn === 'cpuUnits') {
        return null;
    }
    return (
        <Layer index={4}>
            {board.tiles.map((tile, i) => (
                <MouseTile key={i} tile={tile} />
            ))}
        </Layer>
    );
};

export default MouseLayer;
