import { Dispatch } from '@reduxjs/toolkit';
import _ from 'lodash';
import { tileDistance } from 'src/common/helpers';
import { boardMouseClick, endCurrentUnitTurn, selectAttack } from 'src/reducers/boardReducer';
import { Board, BoardLocation, Tile } from './types';
import Unit from './Unit';

type WeightedTile = {
    tile: Tile;
    weight: number;
    attackNow: boolean;
};

export const playAsComputer = (board: Board, dispatch: Dispatch) => {
    if (!(board.currentTurn === 'cpuUnits')) {
        return;
    }

    const selectedUnit = board.selectedUnit;
    if (!selectedUnit) {
        return; // todo
    }

    if (board.selectedAttack) {
        takeWeightedAttack(board, selectedUnit, dispatch);
    } else if (selectedUnit.hasMovementLeft()) {
        takeWeightedMove(board, selectedUnit, dispatch);
    } else {
        dispatch(selectAttack(selectedUnit.getAttacks()[0]));
    }
    // if (selectedUnit.movesRemaining() > 1) {

    //     const randTile = _.sample(tilesCanMoveTo(board, selectedUnit));
    //     if (!randTile) {
    //         dispatch(endCurrentUnitTurn());
    //         return;
    //     }
    //     dispatch(boardMouseClick({ x: randTile.x, y: randTile.y }));
    // } else {
    //     dispatch(endCurrentUnitTurn());
    // }
};

const takeWeightedMove = (board: Board, unit: Unit, dispatch: Dispatch) => {
    let weighedTiles = sortPossibilities(tilesCanMoveTo(board, unit), board);
    if (weighedTiles.length > 0) {
        // console.log(weighedTiles.map(p => [p.attackNow ? 0 : 1, -p.weight]));

        if (_.some(weighedTiles, p => p.attackNow)) {
            dispatch(selectAttack(unit.getAttacks()[0]));
            return;
        }
        weighedTiles = weighedTiles.filter(function (poss) {
            return poss.weight === weighedTiles[0].weight;
        });
        // console.log(
        //     weighedTiles.map(function (p) {
        //         return p.tile.loc.concat(p.weight).join(', ');
        //     })
        // );
        const choice = _.sample(weighedTiles)!.tile;
        dispatch(boardMouseClick({ x: choice.x, y: choice.y }));
    } else {
        dispatch(selectAttack(unit.getAttacks()[0]));
    }
};

const unitsCanAttack = (selectedUnit: Unit, board: Board) => {
    return board.playerUnits.reduce((acc, unit) => {
        const loc = unit.tiles.find(loc => selectedUnit.canAttack(loc, null));
        if (loc) {
            return [...acc, { unit, loc }];
        } else {
            return acc;
        }
    }, [] as { unit: Unit; loc: BoardLocation }[]);
};

const takeWeightedAttack = (board: Board, unit: Unit, dispatch: Dispatch) => {
    const unitsToAttack = unitsCanAttack(unit, board);
    const target = _.minBy(unitsToAttack, ({ unit }) => unit.tiles.length);
    if (target) {
        dispatch(boardMouseClick({ x: target.loc[0], y: target.loc[1] }));
    } else {
        dispatch(endCurrentUnitTurn());
    }
};

