const tilesCanMoveTo = (board: Board, unit: Unit) => {
    return board.tiles.filter(tile => {
        const loc: BoardLocation = [tile.x, tile.y];
        return tile.exists && (!isAnyUnitOn(board, loc) || board.selectedUnit?.isOn(loc)) && unit.canMoveTo(loc);
    });
};

const isAnyUnitOn = (board: Board, loc: BoardLocation) => {
    const units = board.playerUnits.concat(board.cpuUnits);
    return _.some(units, unit => unit.isOn(loc));
};

