const sortPossibilities = (tiles: Tile[], board: Board) => {
    return _.sortBy(
        tiles.map(tile => weighTile(tile, board)),
        p => -p.weight
    );
};

const weighTile = (tile: Tile, board: Board) => {
    const unit = board.selectedUnit!;
    let weight = 0;
    let attackNow = false;
    const unitDist = minDistFromEnemy([tile.x, tile.y], board);
    const unitDistFromNow = minDistFromEnemy(unit.head(), board);

    weight += unitDistFromNow - unitDist;
    const range = unit.getAttacks()[0].range;
    const isCurrentlyInRangeToAttack = unitDistFromNow <= range;
    const wouldBeInRangeToAttackIfMoved = unitDist <= range;

    if (
        isCurrentlyInRangeToAttack &&
        ((!wouldBeInRangeToAttackIfMoved && unit.movesRemaining() === 1) || unit.tiles.length === unit.maxLength)
    ) {
        attackNow = true;
    } else if (wouldBeInRangeToAttackIfMoved) {
        weight += 20;
    }

    if (unit.tiles.length < unit.maxLength && unitWillGrow(unit, tile)) {
        weight += 10;
    }
    return { weight, tile, attackNow } as WeightedTile;
};

const minDistFromEnemy = (tileLoc: BoardLocation, board: Board) => {
    const distances = board.playerUnits.map(unit => unit.tiles.map(loc => tileDistance(loc, tileLoc))).flat();
    return Math.min(...distances);
};

const unitWillGrow = (unit: Unit, tile: Tile) => {
    return unit.tiles.filter(([x, y]) => tile.x === x && tile.y === y).length === 0;
};
