import React from 'react';
import { useDispatch } from 'react-redux';
import { changeRoute } from 'src/reducers/routerReducer';
import levels, { Level } from 'src/lib/levels';
import { selectLevel } from 'src/reducers/boardReducer';

const LevelSelect: React.FC = () => {
    const dispatch = useDispatch();
    const startLevel = (level: Level) => {
        dispatch(selectLevel(level));
        dispatch(changeRoute('/ingame'));
    };

    return (
        <div className='screen-wrapper'>
            <div style={{ width: '100%', height: '100px' }} />
            <div style={{ marginRight: 'auto' }} />
            <div className='menu-button'>Choose Level</div>
            <div className='menu-container'>
                {levels.map((level, n) => (
                    <div key={n} className='menu-level' onClick={() => startLevel(level)}>
                        <div className='menu-level-inside'>{n}</div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default LevelSelect;
