import { Board, Tile } from 'src/lib/types';
import Unit from '../lib/Unit';

const defaultBoard: Board = {
    selectedUnit: null,
    selectedAttack: null,
    currentTurn: 'playerUnits',
    tiles: [],
    uploadLocations: [],
    selectedUploadLocation: null,
    playerUnits: [],
    cpuUnits: [],
    setupPhase: true,
    isWon: false,
    isLost: false,
};

function parseTiles(str: string) {
    return str
        .trim()
        .split('\n')
        .map((line, i) => line.split('').map((letter, j) => ({ x: j, y: i, exists: letter === 'A' } as Tile)))
        .flat();
}

export type Level = {
    name: string;
    initializeBoard: () => Board;
};

