import React, { useState } from 'react';
import { Button } from 'react-bootstrap';
import { useDispatch } from 'react-redux';
import { changeRoute } from 'src/reducers/routerReducer';

const NewGame: React.FC = () => {
    const dispatch = useDispatch();
    const [name, changeName] = useState('');
    return (
        <div className='text-center'>
            <div className='row top' />
            <h1>New Game</h1>
            <label>Name</label>
            <input className='form-control' value={name} onChange={e => changeName(e.target.value)} />
            <div className='row'>
                <Button onClick={() => dispatch(changeRoute('/level-select'))} size='lg' variant='primary'>
                    Start Game
                </Button>
            </div>
            <div className='row'>
                <Button onClick={() => dispatch(changeRoute('/'))} size='lg' variant='primary'>
                    Cancel
                </Button>
            </div>
        </div>
    );
};

export default NewGame;
