import { BoardLocation } from 'src/lib/types';

export const locationsNextTo = ([x, y]: BoardLocation, distance = 1): BoardLocation[] => {
    const locations = [];
    for (let i = -distance; i <= distance; i++) {
        for (let j = -distance; j <= distance; j++) {
            if (Math.abs(i) + Math.abs(j) <= distance && !(i === 0 && j === 0)) {
                locations.push([x + i, y + j] as BoardLocation);
            }
        }
    }
    return locations;
};

export const tileDistance = (tile1: BoardLocation, tile2: BoardLocation) => {
    return Math.abs(tile1[0] - tile2[0]) + Math.abs(tile1[1] - tile2[1]);
};
